<template>
    <div class="charInputNav">
        <form id="wordSubmitBox" @submit="clickSubmit">
                <input type="text" class="form-control" id="wordInputBox" v-model="inputWord" placeholder="  .">
            <button type="submit" class="btn btn-primary" id="submitBtn"></button>
        </form>
    </div>
</template>
  
<script>

export default {
    name: 'charNav',
    props: ['word'],
    data() {
        return {
            inputWord:''
        }
    },
    updated(){
    },
    methods: {
        clickSubmit(){
            event.preventDefault();
            let pattern = /([^- | ^- | ^-\x20])/i;
            if (pattern.test(this.inputWord)) {
                alert("  .");
            }
            else
                this.$emit('generateWord',this.inputWord.replace(/ /g, ''));
        }
    },


}
</script>
  
